import React, { useState, useEffect } from "react";

const text = "Hello World";

const Modal = (text, time=1000) => {
    const [modal, setModal] = useState(false);
    useEffect(() => {
        setTimeout(() => {
            setModal(true);
        }, time)
    },[text]);
    
    return (
        <h3>{modal ? text : null}</h3>
    )
}

export default Modal;